import React, {Component} from 'react'
import Product from './Product'

class MarketForm extends Component {

  constructor(props) {
    super(props);
    this.state = { soma: 0, productValues: {}, products: {}, restaurantName:""};

    this.productList = [
      {name: "Morango ", unidade: {"kg": 20.00, "ma√ßo": 2.00}},
      {name: "Piment√£o amarelo", unidade: {"kg": 10.00, "tonelada": 50000000.00}},
      {name: "Piment√£o verde", unidade: {"ma√ßo": 7.00}},
      {name: "Piment√£o vermelho", unidade: {"kg": 10.00}},
      {name: "Salsa Industrial*", unidade: {"kg": 2.50}},
      {name: "Tomate molho ", unidade: {"kg": 80.00}},
      {name: "Tomate salada", unidade: {"kg": 42.00}},
      {name: "Cebolinha*", unidade: {"kg": 0.29}},
      {name: "Cenoura G", unidade: {"kg": 25.00}},
      {name: "Hortel√£", unidade: {"kg": 3.00}},
      {name: "Lim√£o Tahiti", unidade: {"kg": 9.50}},
      {name: "Manjeric√£o*", unidade: {"kg": 0.30}},
      {name: "Melancia ", unidade: {"kg": 22.95}},
      {name: "Alface crespa", unidade: {"kg": 10.80}},
      {name: "Cebola", unidade: {"kg": 10.00}},
      {name: "Alface crespa", unidade: {"kg": 10.80}},
      {name: "Alface roxa", unidade: {"kg": 20.00}},
      {name: "Couve manteiga", unidade: {"kg": 7.20}},
      {name: "R√∫cula", unidade: {"kg": 17.60}},
      {name: "Agri√£o", unidade: {"kg": 9.40}},
      {name: "Repolho Verde ", unidade: {"kg": 11.60}},
      {name: "Repolho Roxo", unidade: {"kg": 0.00}},
      {name: "Abobrinha", unidade: {"kg": 1.60}},
      {name: "Beringela", unidade: {"kg": 6.60}},
      {name: "Ab√≥bora kabotcha ", unidade: {"kg": 0.00}},
      {name: "Batata doce", unidade: {"kg": 6.50}},
      {name: "Batata boneca", unidade: {"kg": 24.00}},
      {name: "Cenoura", unidade: {"kg": 15.00}},
      {name: "Cebola", unidade: {"kg": 8.00}},
      {name: "Beterraba", unidade: {"kg": 10.00}},
      {name: "Tomate Salada", unidade: {"kg": 29.40}},
      {name: "Abacaxi", unidade: {"kg": 23.40}},
      {name: "Laranja", unidade: {"kg": 28.00}},
      {name: "Melancia ", unidade: {"kg": 1.35}},
    ];
    console.log(this.productList[0]);

    this.handleProductQuantityChange = this.handleProductQuantityChange.bind(this)
    this.onChangeRestaurantName = this.onChangeRestaurantName.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  onChangeRestaurantName(event){
    console.log("onChangeRestaurantName")
    const target = event.target;
    let value = target.value
    console.log(value)
    this.setState({restaurantName: value})
  }


  handleProductQuantityChange(name, val){
    let sum = val.sum
    let qtde = val.qtde
    let unidade = val.unidade
    let unit_price = val.unit_price

    console.log('handleProductQuantityChange*************************', name, val)
    console.log(this.state)
    let productValues = Object.assign({}, this.state.productValues);
    productValues[name] = sum;

    let products = Object.assign({}, this.state.products);
    products[name] = {};
    products[name][unidade] = qtde
    products[name]._unit_price = unit_price
    products[name]._preco_total = sum
    console.log(products)

    this.setState({soma: sum, productValues, products})
  }

  somaGeral(){
    let obj = this.state.productValues
    let soma = Object.keys(obj).reduce((sum,key)=>sum+parseFloat(obj[key]||0),0);
    return (soma).toFixed(2)
  }

  handleChange(event){
    this.setState({value: event.target.value});
    console.log("mudou")
  }

  handleSubmit(event) {
    if(this.state.restaurantName == ""){
      alert("Prrencha o nome do Restaurante.")
      return
    }

  // Criando pedido
  let pedido = Object.assign({}, this.state.products);
  pedido.Restaurante = this.state.restaurantName
  pedido.Date = new Date();

  alert('Seu pedido √©: ' + JSON.stringify(this.state.products));
  console.log(pedido)

  fetch("https://frexco-ecdab.firebaseio.com/produtos.json", {
    method: "POST",
    body: JSON.stringify(pedido)
  })
  .then(function(response) {
    if(response.ok) {
      alert('Tudo certo! :D');

      console.log('Tudo certo!');
    } else {
      alert('Ixi, algo deu errado.. verifique a internet e tente novamente.');

    console.log('Network response was not ok.');
    console.log(response);
    }
    })
  .catch(function(error) {
    console.log('There has been a problem with your fetch operation: ' + error.message);
  });


  event.preventDefault();
  }

  render_product_list(){
    return this.productList.map(i => {
      return <Product key={i.name} info={i} onProductQuantityChange={this.handleProductQuantityChange}/>;
    })
  }

  render() {
    const soma = this.somaGeral()
    return (
      <p style={{alignItems: 'center'}}>
        <div class="container">
        <br />

        <strong>Restaurante: </strong>
        <input type="text" name="Restaurante" onChange={this.onChangeRestaurantName}/>
        <br />
        <table id="cart" class="table table-hover table-condensed">
            <thead>
                <tr>
                    <th style={{width: '40%'}}>Produto</th>
                    <th style={{width: '10%'}}>Unidade</th>
                    <th style={{width: '20%'}}>Pre√ßo</th>
                    <th style={{width: '18%'}}>Quantidade</th>
                    <th style={{width: '22%'}} class="text-center">Subtotal</th>
                </tr>
            </thead>
            <tbody>
              {this.render_product_list()}

            </tbody>
            <tfoot>
                <tr class="visible-xs" hidden>
                    <td class="text-center"><strong>Total R$ <span id='total'>{ soma }</span></strong></td>
                </tr>
                <tr>
                    <td><a href="#" onClick={this.handleSubmit} class="btn btn-warning"><i class="fa fa-angle-left"></i> Enviar pedido</a></td>
                    <td colspan="2" class="hidden-xs"></td>
                    <td class="hidden-xs text-center"><strong>Total R$ { soma }</strong></td>
                    <td><a href="#" class="btn btn-success btn-block" hidden>Checkout <i class="fa fa-angle-right"></i></a></td>
                </tr>
            </tfoot>
        </table>
        </div>
      </p>
    );
  }
}


export default MarketForm;
