import React, {Component} from 'react'

class Product extends Component {
  constructor(props) {
    super(props);
    this.available_units = Object.keys(this.props.info.unidade);
    console.log(this.available_units)
    this.state = { qtde: 0 , sum: 0, unit_selected: this.available_units[0]};

    this.handleChange = this.handleChange.bind(this);
  }

  get_total_price() {
    return (this.state.sum).toFixed(2)
  }


  handleChange(event) {
    console.log("handleChange")
    const target = event.target;
    let value = target.type === 'checkbox' ? target.checked : target.value;
    let qtde = this.state.qtde
    const name = target.name;
    let sum = 0;
    let unit_selected = this.state.unit_selected
    let unit_price = this.props.info.unidade[unit_selected]

    console.log(name, value)

    if (name == "qtde" && value < 0){
      value = 0;
      qtde = 0}

    if (name == "qtde" ){
      qtde = value;
      sum = value * unit_price;
    }

    if (name == "unidade" ){
      console.log("mudando unidade")
      unit_selected = value;
      sum = 0;
      qtde = 0;
      unit_price = this.props.info.unidade[unit_selected]
    }
    console.log("####################", {
      qtde,
      sum,
      unit_selected
    })

    this.setState({
      qtde,
      sum,
      unit_selected
    });

    this.props.onProductQuantityChange(this.props.info.name, {sum: sum, qtde: value, unidade: unit_selected, unit_price: unit_price})
  }

  available_units_options(){
    return this.available_units.map(i => {
      return <option key={i.name}> {i} </option>;
    })
  }

  render() {
    let unit = this.state.unit_selected
    // console.log(this.state, unit)
    let unit_price = this.props.info.unidade[unit]

    return (
        <tr>
            <td data-th="Product">
                <div class="row">
                    <div class="col-sm-2 hidden-xs" hidden><img src="http://placehold.it/100x100" alt="..." class="img-responsive"/></div>
                    <div class="col-sm-10">
                        <h4 class="nomargin">{this.props.info.name}</h4>
                    </div>
                </div>
            </td>
            <td data-th="Unidade">
                <select name="unidade" class="form-control" id="exampleFormControlSelect1" onChange={this.handleChange}>
                  {this.available_units_options()}


                </select>
            </td>
            <td data-th="Price">R$ {(unit_price).toFixed(2)}</td>
            <td data-th="Quantity">
              <input name="qtde" type="number" class="form-control text-center" min="0" value={this.state.qtde} onChange={this.handleChange}/>
            </td>
            <td data-th="Subtotal" class="text-center">R$ {this.get_total_price()}</td>
        </tr>
    );
  }
}

export default Product;
